/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;500&family=Source+Sans+Pro:wght@300;400;600;700;900&family=Work+Sans:wght@200;300;400;500;600;700;800&display=swap');


/* css variables */
:root{
  --primary: #032541;
  --secondary: #21d07a;
  --white: #fff;

}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Source Sans Pro', sans-serif;
}

.container{
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width:768px){
  .container{
    width: 750px;
  }
}

@media (min-width:992px){
  .container{
    width: 970px;
  }
}

@media (min-width:1200px){
  .container{
    width: 1170px;
  }
}


*::before,*::after{
  box-sizing: border-box;

}

ul{
  list-style: none;
  padding: 0;
  margin: 0;
}

.btn{
  padding: 10px 15px;
  background-color: var(--secondary);
  color: var(--primary);
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  border: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  line-height: 1.1;
}

.btn:hover{
  background-color: #1aa762;
  opacity: 1;
  cursor: pointer;
}

.btn:disabled{
  pointer-events: none;
  opacity: 0.5;
}

input:focus{
  outline: none;
}


